import React, { useState } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import SignUp from './Components/SignUp';
import Login from './Components/Login';
import Home from './Components/Home';
import { AuthProvider } from './Auth';
import PrivateRoute from './PrivateRoute'

const App = () => {

  return (
    <AuthProvider>
      <Router>
        <div>
          <PrivateRoute exact path="http://Drew8521.github.io/Artificer/" component={Home} />
          <Route exact path="http://Drew8521.github.io/Artificer/login" component={Login} />
          <Route exact path="http://Drew8521.github.io/Artificer/signup" component={SignUp} />
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
